@import url(https://fonts.googleapis.com/css?family=Abel);

*{
    box-sizing: border-box;
    margin:0;
    padding: 0;
}
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.link{
    text-decoration: none;
    color: white;
}
.link :hover{
    text-decoration: none;
}

a:hover{
    text-decoration: none !important;
    color: #b3b3b3 !important;

}
.navbar__container{
    position: -webkit-sticky !important;
    position: sticky !important;
    top: 0;
    z-index: 10;
}
.navbar{
    background-color:rgb(20, 20, 20);
    height: 4rem;
    align-items: center;
    justify-content: space-between;
}
.navbar__middle{
    text-align: center;
    display: flex;
    align-items: center;
}
#basic-nav-dropdown{
    color: white;
}
.navbar__logo{
    font-size: 1.2rem;
    font-weight: bolder;
}
.navbar__middle > .link{
    padding: 0 1rem;
}
.navbar__right{
    display: flex;
    align-items: center;
}
.navbar__mobile__icon{
    color: white;
    margin-left: 20px;
    font-size: 2rem;
    display: none;
}
.navbar__mobile__icon :hover{
    color: grey;
}
@media screen and (max-width:1200px){
    .navbar__container{
        position: relative !important;
    }
    .navbar__middle{
        display: none;
    }
    .navbar__button{
        display: none;
    }
    .navbar__mobile__icon{
        display: block;
    }
}

.header{
    width: 100%;
    background-color:#355876;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 0px 10px;
    font-size: 1rem;
}
.phone,.address,.social__media{
    width: 30%;
    display: flex;
    align-items: center;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    padding:7px 0px;
}
.phone{
    justify-content: center;
    background-color: #173652;
    padding: 2px;
}
.social__media{
    justify-content: flex-end;
}
@media screen and (max-width:950px){
    .header{
        flex-direction: column;
        grid-row-gap: 10px;
        row-gap: 10px;
        padding:10px 0px;
    }
    .phone,.address,.social__media{
        width: 100%;
        justify-content: center;
        padding:3px 0px;
    }
}
.social__media{
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
}
.social__media :hover{
    color: grey;
}
.sidebar{
    width: 100%;
    height: 100vh;
    background-color: rgb(20,20,20);
    z-index: 999;
    position: fixed;
    transition: 0.3s ease-in-out;
}
.icon{
    color: white;
    font-size: 2rem;
    position: absolute;
    right: 1.2rem;
    top: 3rem;
}
.icon :hover{
    color: grey;
}
.show{
    top: 0%;
    opacity: 100%;
}
.hide{
    top: -100%;
    opacity: 0%;
}
.sidebar-content{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    line-height: 3rem;
}
.bottom{
    position: absolute;
    bottom: 20%;
}
@media screen and (max-width:950px){
    .icon{
        top: 10rem;
    }
}
.hero{
    width: 100%;
    height: 50vh;
    position: relative;
    text-align: center;
    color: #fff;
    background-color: rgb(0, 0, 0);
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
}
.hero__video > video{
    width: 100%;
    height: 50vh;
    object-fit: cover;
    opacity: 50%;

}
.hero__title{
    margin-top: 80px;
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.hero__title >h1{
    font-size: 3rem;
    font-weight: bolder;
}
.hero__title >h4{
    color: darkgrey;
    font-family: monospace !important;
    font-weight: bolder;
}

.hero__buttons >button{
    border-radius:50px;
    background:#01bf71;
    white-space:nowrap;
    padding:10px 22px;
    color: #010606;
    font-size: 16px;
    outline:none;
    border: none;
    cursor:pointer;
    transition: all 0.2s ease-in-out;
    text-decoration:none;
    margin:10px 5px;
}
.hero__buttons >button:hover{
    transition:all 0.2s ease-in-out;
    background:#fff;
    color:#106060;

}
.black{
    color: #010606;
}


@media screen and (max-width:1200px){
    .hero__buttons{
        display:flex;
        flex-direction:column;
        align-items: center;
        justify-content: center;
    }
    .hero__buttons > button{
        width: 260px;
    }
    .hero__video >video{
        height: 100vh;
    }
    .hero{
        height: 100vh;
    }
}
