.scard{
    max-width: 1300px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;
    margin-bottom: 100px;
    align-items: center;
}

.card__image{
    width: 50%;
    text-align:center;
}
.card__image> img{
    width: 500px;
    object-fit: cover;
    padding: 5px;
    border-radius: 12px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    height: 300px;
}
.card__details{
    width: 50%;
    text-align: center;
    color: #6a6d75;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;

}
.card__details > p{
    text-align: justify;
    font-size: 1.2rem;
}
.card__details > h2{
    margin-bottom: 30px;
    font-size: 40px;
    font-weight: 700;
    color: #303236;
    font-family: 'Poppins', sans-serif;
}

@media screen and (max-width:1200px){
    .scard{
        flex-direction: column !important;
        width: 100% !important;
        padding: 0px;
        margin: 20px 20px 0px 0px;
        padding-bottom: 20px;
    }
    .card__image{
        width: 100% !important;
    }
    .card__image> img{
        width: 80vw !important; 
        margin: 20px; 
        min-height: 400px;
    }
    .card__details{
        width:100% !important; 
    }
    .card__details > h2{
        font-size: 30px;
    }
    .card__details >p{
        text-align: justify;
        margin: 20px;
        font-size: 1rem;
        padding: 0px 100px;
    }
}
@media screen and (max-width:768px){
    .card__image > img{
        min-height: auto;
        height: 200px;
    }
    .card__details > p {
        padding: 0px 4px;
    }
}


