    padding: 0.125rem 0.75rem;
    border-radius: 100px;
    white-space: nowrap;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    margin-top: 25px;
}
.card__left{
    width: 35%;
    border-right: 1px solid rgba(0, 0, 0, 0.116);
}
.card__right{
    width: 65%;
    padding: 0px 15px;
}
.avatar > img{
    width: 100px;
    height: 100px;
    border-radius: 50px;
    object-fit: cover;
}
.avatar{
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
}
.more__info > h1{
    color: #224C36;
    font-size: 1.3rem;
    font-weight: bold;
    margin-top: 30px;
    padding-bottom: 0px;
    text-transform: uppercase;
    text-align: center;

}
.others > p{
    font-size: 1rem;
    color: #224C36;
    display: flex;
    justify-content: space-between;
}
.others{
    margin-top: 20px;
    line-height: 0rem;
    color: #224C36;
}
.card__address >p{
    font-size: 1rem;
    bottom: 0px;
    line-height: 1rem;
}
.card__address{
    color: #224C36;
    position: absolute;
    bottom: 0px;
    text-align: center;
}
.card__doctor {
    position: absolute;
    right: 15px;
}
@media screen and (max-width:768px){
    .appointment{
        width: 100%;
        padding: 0px;
        margin: 10px;
    }
    .more__info > h1{
        line-height: 1.2rem;
        margin-top: 60px;
    }
    .avatar > img{
        width: 70px;
        height: 70px;
    }
    .type{
        font-size: 0.6rem;
    }
    .avatar{
        margin-top: 50px;
        margin-bottom: 50px;
    }

}

.appointmentlist{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.loading{
    width: 100%;
    position: absolute;
    top: 50%;
    left: 55%;
    font-size: 3rem;
}
@media screen and (max-width:768px){
    .loading{
        top: 70%;
        left: 45%;
    }
}
